* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
} 

body {
  font-family: 'Poppins', sans-serif;
  background-color: #F1EDC2;
}

img {
  border-radius: 100%;
}

.container {
  height: 70%;
  width: 73%;
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-radius: 20px;
  background-color: #FFFFF3;
}
.woman{
  padding: 20px;
}

button {
  border-radius: 10%;
  padding: 10px;
  border: none;
  font: bold;
  margin: 5px;
  
}

.item-3 {
  margin-top: 10px;
}

.far {
  margin: 5px;
  margin-top: 15px;
  margin-bottom: 5px;
  margin-left: 10px;
  font-size: 2em;
}

.fab {
  margin: 5px;
  margin-top: 15px;
  margin-bottom: 15px;
  font-size: 2em;

}
/** For Tablet View */
@media only screen and (min-width: 600px) {
  .container {
    width: 60%;
    height: 15%;
    margin: 170px auto; 
  }

  img {
    width: 120%;
    height: auto;
  }

  h1{
    font-size: 50px;
  }

  button {
    font-size: 3em;
    padding: 20px;
  }

  p {
    font-size: 2em;
  }

  .fab, .far {
    font-size: 3em;
  }
}

/** For Desktop View */
@media only screen and (min-width: 768px) {
  .container {
    padding: 10px;
    width: 30em;
    height: 45em;
    margin: 70px auto;
  }
  
  img {
      width: 240px;
      height: 230px;
      margin-left: 50px;
    }

    button {
      padding: 15px;
    }

    .fab, .far {
      font-size: 2em;
    }
}

